import React from 'react';
import PropTypes from 'prop-types';

import {Button, Div, Group, Header, Panel, PanelHeader, PanelHeaderBack, Text, CardGrid, ContentCard,} from '@vkontakte/vkui';
import 'bootstrap/dist/css/bootstrap.css'

const Curs3 = ({id, go}) => (
    <Panel id={id}>
        <img  src={process.env.PUBLIC_URL + "/Heko.png"} className="jump"/>
        <img  src={process.env.PUBLIC_URL + "/ud4pstM.png"} className="jump"/>
        <img  src={process.env.PUBLIC_URL + "/Pn1.png"} className="jump"/>
        <img  src={process.env.PUBLIC_URL + "/Mene.png"} className="jump"/>
        <Group header={<Header mode="secondary"> </Header>}>
            <Div>
                <Button stretched size="l" mode="secondary" onClick={go} data-to="persik">
                    Назад
                </Button>
            </Div>
        </Group>
    </Panel>
);


export default Curs3;